:root {
  font-size: 20px;
}

body {
  min-height: 1vh;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

span,
time,
div,
svg {
  color: inherit;
}

@media (max-width: 769px) {
  :root {
    font-size: 16px;
  }
}

@media (max-width: 400px) {
  :root {
    font-size: 14px;
  }
}
